import axios from 'axios';



let localhost = 'http://39.107.94.185:8000';

// 
export const getCategory = params => {
  // console.log(params,'jiequ');
  if ('id' in params){

    return axios.get(`${localhost}/categorys/?id=`+ params.id)
  }
  else {
    console.log('');
    return axios.get(`${localhost}/categorys/`);


    // return axios.get(`./abc.json`)
  }
};

