import {getUserDetail, changeUserDetail} from '../../api/api'
export default {
  name: "UserInfo",
  data() {
    return {
      userDetail: {
        name: '',
        birthday: '',
        email: '',
        mobile: '',
        gender: 'female',
      },
      rules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 30, message: ' 2  30 ', trigger: 'blur'}
        ],
        email: [
          {required: true, message: '', trigger: 'blur'},
          // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        mobile: [
          {required: true, message: '', trigger: 'blur'},
          {min: 11, max: 11, message: '11', trigger: 'blur'}
        ],
        birthday: [
          {type: 'date', required: false, message: '', trigger: 'change'}
        ],
        gender: [
          {required: true, message: '', trigger: 'change'}
        ]
      },

    }
  },
  created(){
    this.getDetails()
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
      console.log(this.userDetail);
      changeUserDetail(this.userDetail).then((response)=>{
        alert('')
      }).catch(function (error) {
          console.log(error)
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getDetails(){
      getUserDetail().then((response)=>{
        this.userDetail = response.data
      }).catch(function (error) {
          console.log(error)
      })
    },
    isMale(){
      if (this.userDetail.gender === 'male') {
        return true
      }
    },
    isFemale(){
      if (this.userDetail.gender === 'female') {
        return true
      }
    },
  }
}



